.frames{
    padding-bottom: 1em;
}

.frame{

        display: block;
        background: #111 url('http://placehold.it/600x600') no-repeat center;
        background-size: cover;
        position: relative;
        top: 1em;
        left: 2em;
        min-height: 200px;
        width: 100vw;

        &:after{
            content: '';
            float: left;
            padding-top: 100%;
        }
        
        + .frame{
            margin-top: 1em;
        }
    
        &.purple{
            &:before{ background: $rose_purple; }
            header{ background: $rose_purple; }
        }
    
        &.green{
            &:before{ background: $lawn_green; }
            header{ background: $lawn_green; }
        }
    
        &.yellow{
            &:before{ background: $summer_yellow; }
            header{ background: $summer_yellow; }
        }
    
        &.maroon{
            &:before{ background: $deep_maroon; }
            header{ background: $deep_maroon; }
        }
    
        &.red{
            &:before{ background: $bloom_red; }
            header{ background: $bloom_red; }
        }
    
        &.black{
            &:before{ background: $off_black }
            header{ background: $off_black; }
        }
        
        &:before{
            content: '';
            width: 100%;
            height: 100%;
            background: $breeze_blue;
            position: absolute;
            z-index: -1;
            top: -1em;
            left: -1em;
            padding-right: 1em;
            padding-bottom: 1em;
            display: block;
        }
    
        header{
            background:$breeze_blue;
            color: #fff;
            padding: 1em 2em 1em 0;
            text-align: left;
            display: inline-block;
            position: absolute;
            top: 2em;
            left: 0;
            max-width: 95%;
            box-sizing: border-box;
        }
    
        .buttons{
            position: absolute;
            bottom: 10%;
            left: -2em;
            width: 100%;
            display: block;

        }

        &:hover{
            .button{
                @extend .button:hover;
            }
        }

        
    
    }

@media screen and (min-width: 650px){
    .frames{
        padding: 0;
        margin: 3em!important;
        width: inherit!important;
    }

    .frame{
        top: auto;
        left: auto;
        font-size: .8em;

        + .frame{
            margin: auto;
        }
    
        .buttons{
            left: 0;
        }
    
        &:nth-child(3n+1){
            left: -2em;
        }
    
        &:nth-child(3n){
            right: -2em;
        }
    
        &:nth-child(n+4){
            margin-top: 2em;
        }
        
        &:before{
            padding-bottom: 0;
        }
    }
}

@media screen and (min-width: 1024px){ 
    .frames{
        margin: 4em 8em!important;
    }

    .frame{
        font-size: 1em;
    }
}

@media screen and (max-width: 650px){
    .grid > .column.frame{
        width: 100vw;
        max-height: 350px;
    }
}